import { storiesOf } from '@storybook/react';
import React from 'react';

import { text } from '@storybook/addon-knobs';

import { Coupon, StorybookContainer, StorybookInfo } from '..';

import { couponKnobs } from '../knobs';

const options = {
    propTablesExclude: [StorybookInfo],
};

storiesOf('Components', module)
    .addParameters({
        info: options,
    })
    .addWithJSX(
        'Coupon',
        () => {
            const coupon = (
                <Coupon
                    couponLabel={text(couponKnobs.couponLabel.label, couponKnobs.couponLabel.default)}
                    codeLabel={text(couponKnobs.codeLabel.label, couponKnobs.codeLabel.default)}
                    badgeLabel={text(couponKnobs.badgeLabel.label, couponKnobs.badgeLabel.default)}
                    code={text(couponKnobs.code.label, couponKnobs.code.default)}
                    saving={text(couponKnobs.saving.label, couponKnobs.saving.default)}
                    expiry={text(couponKnobs.expiry.label, couponKnobs.expiry.default)}
                    footnote={text(couponKnobs.footnote.label, couponKnobs.footnote.default)}
                />
            );

            return <StorybookContainer type="feature">{coupon}</StorybookContainer>;
        },
        { skip: 1, showDefaultProps: false },
    );
